<template>
    <div class="water-electricity-zhu-chart">
        <el-card class="water-electricity-line">
            <el-date-picker @change="updateData" value-format="yyyy-MM" style="width: 120px;" v-model="defaultDate" type="month" placeholder="选择月">
            </el-date-picker>
            <h4 style="display: inline;">的用水量和用电量排名</h4>
            <div style="height: 500px" id="water-electricity-main-zhu">
            </div>

        </el-card>
    </div>
</template>
<script>
import { waterAndElectricityRank } from '@/api/meterReading'
export default {
    data() {
        return {
            defaultDate: '',
            echartData: {
                roomAllName: [],
                waterReading: [],
                electricityReading: []
            }
        }
    },
    mounted() {
        //初始化时间
        this.currentDate()
        //获取后端数据
        this.initData()

    },
    methods: {
        //获取数据并初始化
        initData(){
            //重置
            this.echartData= {
                roomAllName: [],
                waterReading: [],
                electricityReading: []
            }
            waterAndElectricityRank(this.defaultDate).then(res => {
                res.data.forEach(item => {
                    let roomName = `${item.buildingName}(${item.houseNumber})`
                    this.echartData.roomAllName.push(roomName)
                    this.echartData.waterReading.push(item.waterReading)
                    this.echartData.electricityReading.push(item.electricityReading)
                })
                this.echartsInit()
            })
        },
        //初始化echarts
        echartsInit() {
            //因为初始化echarts 的时候，需要指定的容器 id='main'
            this.$echarts.init(document.getElementById('water-electricity-main-zhu')).setOption(
                {
                    title: {
                        // text: '用水量和用电量排名'
                    },
                    tooltip: {},
                    legend: {
                        data: ['用水量', '用电量']
                    },
                    yAxis: {
                        type: 'category',
                        data: this.echartData.roomAllName
                    },
                    xAxis: [
                        {
                            type: 'value',
                            name: '用水量',
                            position: 'top',
                            axisLabel: {
                                formatter: '{value} m³'
                            }
                        },
                        {
                            type: 'value',
                            name: '用电量',
                            position: 'bottom',
                            axisLabel: {
                                formatter: '{value} kW·h'
                            }
                        }
                    ],
                    series: [
                        {
                            name: '用水量',
                            data: this.echartData.waterReading,
                            type: 'bar',
                            barWidth: 13,
                            barCategoryGap: '50%',
                            itemStyle: {
                                normal: {
                                    color: '#3398DB'
                                }
                            }
                        },
                        {
                            name: '用电量',
                            data: this.echartData.electricityReading,
                            type: 'bar',
                            barWidth: 13,
                            barCategoryGap: '50%',
                            itemStyle: {
                                normal: {
                                    color: '#FFCC33'
                                }
                            },
                            xAxisIndex: 1
                        }
                    ]
                }
            )
        },
        //获取当前时间
        currentDate() {
            var now = new Date();
            var year = now.getFullYear();
            var month = now.getMonth() + 1;
            month = month < 10 ? '0' + month : month; // 如果月份小于10，前面补0
            var result = year + '-' + month;
            // console.log(result); // 输出如：2023-03
            this.defaultDate = result

        },
        //触发更新
        updateData(){
            if(this.defaultDate!=null&&this.defaultDate!="")
            //获取后端数据
            this.initData()

        }
    }
}
</script>
<style scoped></style>
