<template>
    <div class="building-pie-chart">
        <el-card class="bing">
            <div style="height: 395px" id="main">
            </div>
        </el-card>
    </div>
</template>

<script>
import { roomCountInBuilding } from '@/api/room'
//通过this.$echarts来使用
export default {
    name: "page",
    data(){
        return{
            data:[]

        }
    },
    created(){
        roomCountInBuilding().then(res =>{
            res.data.forEach((item)=>{
                let temp = {
                    value: item.roomCount,
                    name: item.buildingName
                }
                this.data.push(temp)
            })
            // 在通过mounted调用即可
            this.echartsInit()
        })
    },
    mounted() {
        
    },
    methods: {
        //初始化echarts
        echartsInit() {
            //柱形图
            //因为初始化echarts 的时候，需要指定的容器 id='main'
            this.$echarts.init(document.getElementById('main')).setOption({
                title: {
                    text: '各个楼盘房屋数占比',
                    // subtext: 'Fake Data',
                    left: 'center'
                }, 
                tooltip: {
                    formatter: '{b}: {c}'
                },
                legend: {
                    orient: 'vertical',
                    left: 'left'
                },
                series: [
                    {
                        name: 'Access From',
                        type: 'pie',
                        radius: ['20%', '50%'],
                        data: this.data,
                        emphasis: {
                            itemStyle: {
                                shadowBlur: 10,
                                shadowOffsetX: 0,
                                shadowColor: 'rgba(0, 0, 0, 0.5)'
                            }
                        }
                    }
                ]
            })
        }

    }
}
</script>

<style scoped>
.bing{
    margin: 0 5px 5px 0;
}
</style>