<template>
    <div class="renter-line-chart">
        <el-card class="line">
            <div style="height: 395px" id="main-line">
            </div>
        </el-card>
    </div>
</template>


<script>
import { rentingWeek } from '@/api/renting'
export default {
    data() {
        return {
            //变量名
            xdata:[],
            //数据
            ydata:[]
        }
    },
    mounted() {
        this.data=[]
        rentingWeek().then(res=>{
            // 获取当前日期
            // 获取星期几，0表示星期日，1表示星期一，以此类推
            // 定义星期数组
            const weekdays = ['日', '一', '二', '三', '四', '五', '六'];
            // 根据星期几获取对应的文字
            res.data.forEach(item=>{
                const date = new Date(item.date);
                const weekday = date.getDay();
                const weekText = weekdays[weekday];
                this.xdata.push(date.getDate()+"("+weekText+")")
                this.ydata.push(item.rentCount)
            })
            this.echartsInit()
        })
    },
    methods: {
        //初始化echarts
        echartsInit() {
            //柱形图
            //因为初始化echarts 的时候，需要指定的容器 id='main'
            this.$echarts.init(document.getElementById('main-line')).setOption(
                {
                    title: {
                        text: '最近一周出租数',
                        left: 'center'
                    },
                    tooltip: {
                        trigger: 'item'
                    },
                    xAxis: {
                        type: 'category',
                        data: this.xdata
                    },
                    yAxis: {
                        type: 'value',
                        axisLabel: {
                            formatter: function (value) {
                                if (value % 1 === 0) {
                                    return value;
                                } else {
                                    return '';
                                }
                            }
                        }
                    },
                    series: [
                        {
                            data: this.ydata,
                            type: 'line'
                        }
                    ]
                })
        }
    }
}
</script>

<style scoped>
.line{
    margin: 0 5px 5px 0;
}
</style>
