<template>
    <div>
        <el-card>
            <div slot="header" style="height: 20px;">
                <span>租户总数</span>
            </div>
            <div class="card-content">
                <div class="card-icon">
                    <i class="el-icon-user"></i>
                </div>
                <div class="card-text">
                    <span class="number">{{ tenantCount }}</span>
                    <span>人</span>
                </div>
            </div>
        </el-card>
    </div>
</template>
<script>
import { getTotalRenter } from '@/api/renter'
export default {
    data() {
        return {
            tenantCount: ""
        };
    },
    created() {
        this.getTotalRenter()
    },
    methods: {
        getTotalRenter() {
            getTotalRenter().then(res => {
                this.tenantCount = res.data
            })
        }
    }

};
</script>
<style scoped>
.card-content{
    height: 52px;

}

</style>